<template>

    <form>

        <i class="glyphicon glyphicon-search"></i>
        <div class="input-group" v-bind:class="{ 'input-group-lg': isLarge }">
            <div class="input-group-prepend">
                <font-awesome-icon icon="search" />
            </div>
            <label for="header-search" class="sr-only">Search</label>
            <input class="form-control" v-bind:class="{ 'form-control-lg': isLarge }" placeholder="Find a venue by location or keyword" name="q" id="header-search" type="text" v-model="q">
            <div class="input-group-append">
              <button type="submit" class="btn" :class="btnClass" @click.stop.prevent="submit()">Search</button>
            </div>
        </div>

    </form>

</template>

<script>
export default {
  name: "Search",
  data(){
    return {
      q : this.$store.state.queryString
    }
  },
  props: {
    color: String,
    isLarge: Boolean
  },
  computed: {
    btnClass: function () {
        if ( this.color != null ) {
            return 'btn-' + this.color
        }else{
            return 'btn-purple'
        }
    },
  },
  methods: {
    submit(){
        this.$store.commit('query', this.q);
        this.$router.push("/search");
    }
  },
  mounted() {
    if(this.$router.currentRoute.name != 'search'){
        this.$store.commit('query', null);
        this.q = null;
    }
  }
};
</script>

